<template>
  <div class="app">
    <transition name="fade">
      <b-btn @click="backToTop" v-if="showing" variant="warning" class="my-btn floating-button">
        <icon class="text-center" name="angle-up" scale="2.4"></icon>
      </b-btn>
    </transition>
    <router-view/>
  </div>
</template>

<script>
import axios from 'axios'
import { mapActions } from 'vuex'
import { API_URL } from '@/libraries/constants'

export default {
  name: 'app',
  data () {
    return {
      showing: false
    }
  },
  async mounted () {
    // prevent autoscroll to previous position when refesh
    if ('scrollRestoration' in history) {
      if (this.$route.query.id) {
        history.scrollRestoration = 'manual'
      } else {
        history.scrollRestoration = 'auto'
      }
    }
    window.addEventListener('scroll', this.handleScroll)
    this.setResult((await axios.get(API_URL)).data)
    this.setLoading(false)
  },
  methods: {
    ...mapActions(['setResult', 'setLoading']),
    handleScroll () {
      this.showing = window.scrollY >= 500
    },
    backToTop () {
      this.$SmoothScroll(document.body, 500)
    }
  }
}
</script>

<style>
.app {
  /*font-family: 'Avenir', Helvetica, Arial, sans-serif;*/
  /*color: #dddddd;*/
  font-family: 'Kanit', sans-serif;
}

.my-btn {
  cursor: pointer;
  text-align: center;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.3);
  transition: all 300ms !important;
}

.my-btn:hover {
  transform: translateY(2px);
}

.floating-button {
  position: fixed;
  bottom: 30px;
  right: 30px;
  border-radius: 25px !important;
  width: 50px;
  height: 50px;
  color: white !important;
  z-index: 4;
  padding-top: 0.2em !important;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}

.datepicker-input {
  color: cornflowerblue;
  cursor: pointer;
  background: none;
  font-weight: 300;
  border: none;
  padding: 0;
  border-bottom-width: 4px;
  border-bottom-style: dotted;
  border-bottom-color: black;
  width: 8.6em;
  transition: all 300ms;
}

.datepicker-input:hover {
  filter: brightness(130%);
}

.light {
  font-weight: 200;
}

.medium {
  font-weight: 300;
}

.logo {
  max-height: 90px;
  margin: 20px;
}
</style>



